/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { getItemLink } from "Common/settings"

function LinkNormal({
  type,
  payload,
  linkTo,
  manualLink,
  target,
  classNames,
  children,
}) {
  const className = "wps-" + type + "-link" + " " + classNames

  const linkCSS = css`
    text-decoration: none;
    display: block;

    .wps-product-image {
      &:hover {
        cursor: pointer;
      }
    }
  `

  function getTarget(target) {
    if (target) {
      return target
    }

    return "_blank"
  }

  return (
    <a
      href={wp.hooks.applyFilters(
        "misc.linkHref",
        manualLink ? encodeURI(manualLink) : getItemLink(payload, type, linkTo),
        linkTo
      )}
      className={className}
      css={linkCSS}
      aria-label="Product Link"
      target={wp.hooks.applyFilters(
        "misc.linkTarget",
        getTarget(target),
        type,
        payload
      )}
    >
      {children}
    </a>
  )
}

export default LinkNormal
